import {
  AfterContentInit, AfterViewInit, Component, ContentChildren, ElementRef, EventEmitter, Input, OnChanges, OnDestroy,
  OnInit, Output, QueryList, Renderer2, SimpleChanges, ViewChild,
} from '@angular/core';
@Component({
  selector: 'amexio-menu-ce1',
  templateUrl: './menu.component.html',
})
// tslint:disable-next-line:component-class-suffix
export class AmexioMenuCEComponent1 implements OnInit {

  constructor() { }

  @Input() data: any;

  @Input('font') font = 'Titillium Web';

  @Input() size: string;

  @Input() color: string;

  @Input('animation-type') type: any;

  @Input('menu-positioning') position = 'left';

  @Input('menu-alignment') menuAlignment = 'left';
  animatedClass: any;

  ngOnInit() {
    this.data.forEach((element: any) => {
      element['hover'] = false;
    });

    if (this.type) {
      this.animatedClass = 'bmenu' + this.type;
    }
  }
}
